.login-form {
    width: 540px;
    margin: 50px auto;
    
}
.login-form form {
    margin-bottom: 15px;
    background: #f7f7f7;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 30px;
}
.login-form h2 {
    margin: 0 0 15px;
}
.form-control, .btn {
    min-height: 38px;
    border-radius: 2px;
}
.btn {        
    font-size: 15px;
    font-weight: bold;
    
}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
    margin-bottom: 10px;
  }
.particle{
    position: absolute;
    height:100%;
    width: 100%;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#051937), color-stop(#004d7a), color-stop(#008793), color-stop(#00bf72), to(#a8eb12));
    background-image: -webkit-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
    background-image: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
    z-index: -2;
}
.particleJs{
    position:absolute;
    z-index: -1;
    width:100%;
    height:100%

}
.navBar{
    font-size: 20px;
    float:right;
}
.wrapper{
    display: block;
    margin-left: auto;
    margin-right: auto; 
}
.img-thumbnail{
    width:70px;
    height:70px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
   
}

.background-gradient{
    background-image: -webkit-linear-gradient(330deg, #89f7fe 0%, #66a6ff 100%);
    background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
    height: 100vh;
    overflow: auto;
}

.btns{
    background-image: -webkit-gradient(linear, left top, right top, from(#434343), to(black));
    background-image: -webkit-linear-gradient(left, #434343 0%, black 100%);
    background-image: linear-gradient(to right, #434343 0%, black 100%);
    margin: 20px auto;
    border-radius: 10px;
    width:90%;
    margin-top: 10%;
 

}

