.card-wrapper {
  margin: 5px;
  margin-bottom: 5px;
}
.card-subtitle {
  color: white;
  font-size: 16px;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  height: 250px;
  width: 250px;
}
.card-title {
  color: white;
  text-decoration: underline;
  font-size: 20px;
  margin-top: 30px;
  margin-bottom: 10px;
  text-align: center;
  font-weight: bold;
  display: inline-block;
  width: 100%;
}
.center-block {
  margin-left: 50%;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.card-text {
  color: white;
  font-size: 20px;
  text-align: left;
}

.container {
  padding: 2px 16px;
}

.card-time {
  margin: 10px auto;
}
.text-center {
  margin: 20px;
}

